.button {
  position: relative;
  width: 12.625rem;
  font-size: 0.875rem;
  font-family: "Gotu", sans-serif;
  background-color: #408f95;
  color: #fff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0.625rem;
  line-height: 1.5714285714;
  border: 1px solid #408f95;
  z-index: 10;
}

.button.button--white {
  background-color: #fff;
  color: #408f95;
  border: 1px solid #fff;
}

.button::after {
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button.button--white::after {
  background-color: #408f95;
}

.button::before {
  position: absolute;
  content: "";
  border: 1px solid #408f95;
  top: 0.25rem;
  right: -0.25rem;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.button.button--white::before {
  border: 1px solid #fff;
}

.button span {
  display: inline-block;
  position: relative;
}

.button span::after {
  display: inline-block;
  margin-left: 0.5rem;
  content: "";
  background-image: url(../images/common/button-arrow_1.svg);
  width: 2.5rem;
  height: 0.375rem;
  background-repeat: no-repeat;
  background-size: contain;
  transform: scale(1.3);
}

.button.button--white span::after {
  background-image: url(../images/common/button-arrow_2.svg);
}

.button:hover {
  color: #408f95;
}

.button.button.button--white:hover {
  color: #fff;
}

.button:hover span::after {
  content: "";
  background-image: url(../images/common/button-arrow_2.svg);
  width: 2.5rem;
  height: 0.375rem;
  background-repeat: no-repeat;
  background-size: contain;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button.button--white:hover span::after {
  background-image: url(../images/common/button-arrow_1.svg);
}

